} */



.tile {
    display: inline-flex;
    flex-wrap: wrap;
    /* I had 2px margins showing up on my tile rows  
        needed to do -2px margin on top and bottom 
        to remove them and lign up the map */
    margin-top: -2px;
    margin-bottom: -2px;
    align-content: center;
    background-color: #578036;
}

